import $http from '@/config/requestConfig'
const platform = uni.getSystemInfoSync().platform;
export default {
	/****************z-nav-bar*******************/
	// 
	// 
	// 
	mainPagePath: ['pages/home/home', 'pages/my/my', 'pages/demo/common', 'pages/template/common', 'pages/apiDemo/common', "pages/template/addTemplate"],
	//
	homePath: '/pages/template/addTemplate',
	
	/****************zhouWei-APPUpdate*******************/
	// ajax
	getServerNo: (version, isPrompt = false, callback) => {
		let httpData = {
			version: version.versionCode,
			// 
		    versionName: version.versionName,
			// setupPage
			setupPage: isPrompt   
		};
		if (platform == "android") {
			httpData.type = 1101;
		} else {
			httpData.type = 1102;
		}
		/* 
		 * version: 
		 * versionName: 
		 * type11011102IOS
		 */
		/***********************************/
		// 
		$http.get("api/common/v1/app_version", httpData,{
			isPrompt: isPrompt
		}).then(res => {
			/* res
			 * | 	     |  	| 	    | 
			 * | -------------|--------- | --------- | ------------- |
			 * | versionCode	 | y	    | int	    |         |
			 * | versionName	 | y	    | String	|       |
			 * | versionInfo	 | y	    | String	|       |
			 * | updateType	     | y	    | String	| forcibly = , solicit = , silent =  |
			 * | downloadUrl	 | y	    | String	| IOSstore,apkwgt  |
			 */
			if (res && res.downloadUrl) {
				// updateType
				if(res.updateType){
					callback && callback(res);
				} else {
					if(res.forceUpdate){
						res.updateType = "forcibly";
					} else {
						res.updateType = "solicit";
					}
					callback && callback(res);
				}
			} else if (isPrompt) {
				uni.showToast({
					title: "",
					icon: "none"
				});
			}
		});
		/***********************************/
	},
	// 
	appUpdateColor: "f00",
	//  '/static/demo/ic_attention.png'
	appUpdateIcon: ''
}