import base from '@/config/baseUrl';
import store from '@/store';
class socket {
	constructor(options) {
		//
		this.socketUrl = base.socketUrl;
		this.socketStart = false;
		this.monitorSocketError();
		this.monitorSocketClose();
		this.socketReceive();
	}
	init(callback) {
		const _this = this;
		if (base.socketUrl) {
			if(this.socketStart){
				console.log('webSocket');
			}else{
				uni.connectSocket({
					url: this.socketUrl,
					method: 'GET'
				});
				uni.onSocketOpen((res) => {
					this.socketStart = true;
					callback && callback();
					console.log('WebSocket');
				});
				setTimeout(() => {
					_this.getHeartbeat();
				}, 5000);
			}
		}else{
			console.log('config/baseUrl socketUrl');
		}
	}
	//Socket
	send(data, callback) {
		const _this = this;
		if (store.state.userInfo.uid) {
			data.userUid = store.state.userInfo.uid;
		}
		console.log(data);
		uni.sendSocketMessage({
			data: JSON.stringify(data),
			success: () => {
				callback && callback(true);
			},
			fail: () => {
				callback && callback(false);
			}
		});
	}
	//Socket
	socketReceive() {
		const _this = this;
		uni.onSocketMessage(function(res) {
			let data = JSON.parse(res.data);
			console.log('', data);
			_this.acceptMessage && _this.acceptMessage(data);
		});
	}
	//Socket
	closeSocket() {
		uni.closeSocket();
		_this.socketStart = false;
	}
	//Socket
	monitorSocketClose() {
		const _this = this;
		uni.onSocketClose(function(res) {
			console.log('WebSocket ');
			_this.socketStart = false;
			setTimeout(function() {
				_this.init();
			}, 3000);
		});
	}
	//Socket
	monitorSocketError() {
		const _this = this;
		uni.onSocketError(function(res) {
			_this.socketStart = false;
			console.log('WebSocket');
		});
	}
	//
	getHeartbeat() {
		const _this = this;
		this.send({
			type: "",
			userUid: store.state.userInfo.userUid
		}, (val) => {
			setTimeout(() => {
				if (val) {
					_this.getHeartbeat();
				} else {
					_this.init();
				}
			}, 10000);
		});
	}
};
const mySocket = new socket();
export default mySocket;
