import store from '@/store';
import $http from '@/config/requestConfig'
import base from '@/config/baseUrl';



let code = "";
let loginStart = true;
let userInfo = {
	token: ""
};
let lastPageUrl = "";
// 
function onLogin(type = "judge",callback) {
	//
	if (loginStart) {
		loginStart = false;
		const _this = this;
		let platform;

		platform = 'weixin';







		uni.login({
			provider: platform,
			success: function(loginRes) {
				if (loginRes.errMsg == 'login:ok') {
					// 
					uni.getUserInfo({
						provider: platform,
						success: function(infoRes) {
							let httpData = {
								wxSmallCode: loginRes.code, //code
								iv: infoRes.iv, //
								encryptedData: infoRes.encryptedData //
							};
							// store.state.chatScenesInfo
							if(store.state.chatScenesInfo.invite){
								// 
								httpData.invite = store.state.chatScenesInfo.invite;
							}
							$http.post('api/open/v1/login', httpData).then(res => {
									loginStart = true;
									store.commit('setUserInfo', res);
									callback && callback();
									if (res.nullUserInfo) {
										// 
										store.commit('setBindUserInfoShow', true);
									} else {
										uni.showToast({
											title: ""
										});
									}
								}, err => {
									loginStart = true;
								});
						}
					});
				}
			}
		});
	}
}
//
function judgeLogin(callback, type = "judge"){
    if(store.state.chatScenesInfo.scene == 1154){
        uni.showToast({
        	title: '',
            icon: "none"
        });
    } else {
        let storeUserInfo = store.state.userInfo;
        if(!storeUserInfo.token){ // nvuevuex
            storeUserInfo = uni.getStorageSync("userInfo");
        }
        if (type != "force" && storeUserInfo.token) {
            callback();
        } else if (storeUserInfo.token && !storeUserInfo.phone) {
            if (type == "force") {
                uni.navigateTo({
                    url: '/pages/user/bindPhone'
                });
            } else {
                uni.showModal({
                    title: "",
                    content: "~",
                    confirmText: "",
                    cancelText: "",
                    success: (res) => {
                        if (res.confirm) {
                            uni.navigateTo({
                                url: '/pages/user/bindPhone'
                            });
                        }
                    }
                });
            }
        } else {

            onLogin(type, callback);





















        }
    }
}
