import request from "@/uni_modules/zhouWei-request/js_sdk/request";
import store from '@/store';
import base from '@/config/baseUrl';






import {
	onLogin
} from '@/config/login';

let version_code = '';










//newrequest
let $http = new request({
	//
	baseUrl: base.baseUrl,
	//
	fileUrl: base.baseUrl,
	// url
	defaultUploadUrl: "api/common/v1/upload_image",
	// 
	defaultFileName: "file",
	//content-type
	header: {
		'Content-Type': 'application/json;charset=UTF-8',
		// 'project_token': base.projectToken, //token
	}
});
// token
$http.getQnToken = function(callback){
	//
	$http.get("api/common/v1/qn_upload").then(data => {
		/*
		 *
		 *visitPrefix:
		 *token:token
		 *folderPath:
		 *region:  SCN
		 */
		callback({
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath
		});
	});
}
// token
$http.getAliToken = function(callback){
	//
	$http.get("api/open/v1/ali_oss_upload").then(data => {
		/*
		 *
		 *visitPrefix:
		 *folderPath:
		 *accessKeyId: ossID
		 *accessKeySecret: oss
		 *timeout: 1800000
		 */
		callback({
			accessKeyId: data.accessKeyId,
			accessKeySecret: data.accessKeySecret,
			visitPrefix: data.visitPrefix,
			folderPath: data.folderPath,
			timeout: 1800000
		});
	});
}
//
$http.requestStart = function(options) {
	console.log("", options);
	if (options.load) {
		//
		store.commit("setLoadingShow", true);
	}
	// 
	if (options.method == "FILE") {
		// : options.maxSize 
		let maxSize = options.maxSize || '';
		for (let item of options.files) {
			if(item.fileType == 'image'){
				if (maxSize && item.size > maxSize) {
					setTimeout(() => {
						uni.showToast({
							title: "",
							icon: "none"
						});
					}, 500);
					return false;
				}
			} else if(item.fileType == "video"){
				if (item.duration < 3) {
					setTimeout(() => {
						uni.showToast({
							title: "3",
							icon: "none"
						});
					}, 500);
					return false;
				}
			}
		}
	}






	//token
	let storeUserInfo = store.state.userInfo;
	if(!storeUserInfo.token){ // nvuevuex
	    storeUserInfo = uni.getStorageSync("userInfo");
	}
	if (storeUserInfo.token) {
		options.header['user_token'] = storeUserInfo.token;
	};
	return options;
}
//
$http.requestEnd = function(options) {
	//
	if (options.load) {
		// 
		store.commit("setLoadingShow", false);
	}
}
