export const state = {
  //
  userInfo: {},
};
export const mutations = {
  //
  setUserInfo(state, data) {
  	if (data) {
  		state.userInfo =  Object.assign({}, state.userInfo,data);




  		uni.setStorageSync('userInfo', state.userInfo);

  	}
  },
  // APP
  emptyUserInfo(state) {
  	state.userInfo = {};




  	uni.removeStorageSync("userInfo");

  },
};
export const actions = {
  
};
