const qiniuUploader = require("./qiniuUploader");
const aliUploader =  require('./aliUploader');  
//
export const randomChar = function(l, url = "") {
	const x = "0123456789qwertyuioplkjhgfdsazxcvbnm";
	let tmp = "";
	let time = new Date();
	for (let i = 0; i < l; i++) {
		tmp += x.charAt(Math.ceil(Math.random() * 100000000) % x.length);
	}
	return (
		"file/" +
		url +
		time.getTime() +
		tmp
	);
}
//
export const chooseImage = function(data) {
	return new Promise((resolve, reject) => {
		uni.chooseImage({
			count: data.count || 9, //9
			sizeType: data.sizeType || ['original', 'compressed'], //
			sourceType: data.sourceType || ['album', 'camera'], //
			success: function(res) {
				for (var i = 0; i < res.tempFiles.length; i++) {
					res.tempFiles[i].fileType = "image"
				}
				resolve(res.tempFiles);
			},
			fail: err => {
				reject({
					errMsg: err.errMsg, 
					errCode: err.errCode, 
					statusCode: 0,
				});
			}
		});
	});
}
//
export const chooseVideo = function(data) {
	return new Promise((resolve, reject) => {
		uni.chooseVideo({
			sourceType: data.sourceType || ['album', 'camera'], //
			compressed: data.compressed || false, // true
			maxDuration: data.maxDuration || 60, // 60 
			camera: data.camera || 'back', //'front''back''back'
			success: function(res) {
				let files = [{
					path: res.tempFilePath,
					fileType: "video"
				}];

				files[0].duration = res.duration;
				files[0].size = res.size;
				files[0].height = res.height;
				files[0].width = res.width;




				resolve(files);
			},
			fail: err => {
				reject({
					errMsg: err.errMsg, 
					errCode: err.errCode, 
					statusCode: 0,
				});
			}
		});
	});
}
// 
export const qiniuUpload = function(requestInfo, getQnToken) {
	return new Promise((resolve, reject) => {
		if (Array.isArray(requestInfo.files)) {
			let len = requestInfo.files.length;
			let fileList = new Array;
			if (getQnToken) {
				getQnToken(qnRes => {
					/*
					 *
					 *visitPrefix:
					 *token:token
					 *folderPath:
					 *region:  SCN
					 */
                    let prefixLen = qnRes.visitPrefix.length;
                    if(qnRes.visitPrefix.charAt(prefixLen - 1) == '/'){
                        qnRes.visitPrefix = qnRes.visitPrefix.substring(0, prefixLen - 1)
                    }
					uploadFile(0);

					function uploadFile(i) {
						let item = requestInfo.files[i];
						let updateUrl = randomChar(10, qnRes.folderPath);
						let fileData = {
							fileIndex: i,
							files: requestInfo.files,
							...item
						};
						if (item.name) {
							fileData.name = item.name;
							let nameArr = item.name.split(".");
							updateUrl += "." + nameArr[nameArr.length - 1];
						}
						// 
						qiniuUploader.upload(item.path || item, (res) => {
							fileData.url = res.imageURL;
							requestInfo.onEachUpdate && requestInfo.onEachUpdate({
								url: res.imageURL,
								...fileData
							});
							fileList.push(res.imageURL);
							if (len - 1 > i) {
								uploadFile(i + 1);
							} else {
								resolve(fileList);
							}
						}, (error) => {
							reject(error);
						}, {
							region: qnRes.region || 'SCN', //
							domain: qnRes.visitPrefix, // bucket 
							key: updateUrl,
							uptoken: qnRes.token, //  uptoken
							uptokenURL: 'UpTokenURL.com/uptoken' // 
						}, (res) => {
							console.log(requestInfo);
							requestInfo.onProgressUpdate && requestInfo.onProgressUpdate(Object.assign({}, fileData, res));
							// console.log('', res.progress)
							// console.log('', res.totalBytesSent)
							// console.log('', res.totalBytesExpectedToSend)
						});
					}
				});
			} else {
				reject({
					errMsg: "getQnToken",
					statusCode: 0
				});
			}
		} else {
			reject({
				errMsg: "files ",
				statusCode: 0
			});
		};
	});
}
// oss
export const aliUpload = function(requestInfo, getAliToken) {
	return new Promise((resolve, reject) => {
		if (Array.isArray(requestInfo.files)) {
			let len = requestInfo.files.length;
			let fileList = new Array;
			if (getAliToken) {
				getAliToken(aliRes => {
					/*
					 *
					 *visitPrefix:
					 *folderPath:
					 *accessKeyId: ossID
					 *accessKeySecret: oss
					 * timeout: 
					 */
					let aliyunOssKey = aliUploader({
						accessKeyId: aliRes.accessKeyId,
						accessKeySecret: aliRes.accessKeySecret,
						timeout: aliRes.timeout
					});
                    let prefixLen = aliRes.visitPrefix.length;
                    if(aliRes.visitPrefix.charAt(prefixLen - 1) == '/'){
                        aliRes.visitPrefix = aliRes.visitPrefix.substring(0, prefixLen - 1)
                    }
					uploadFile(0);

					function uploadFile(i) {
						let item = requestInfo.files[i];
						let updateUrl = randomChar(10, aliRes.folderPath);
						let fileData = {
							fileIndex: i,
							files: requestInfo.files,
							...item
						};
						if (item.name) {
							fileData.name = item.name;
							let nameArr = item.name.split(".");
							updateUrl += "." + nameArr[nameArr.length - 1];
						}
						if (item.path) {
							let nameArr = item.path.split(".");
							updateUrl += "." + nameArr[nameArr.length - 1];
						}
						uni.uploadFile({
						  url: aliRes.visitPrefix, // URL
						  filePath: item.path,
						  name: 'file', // file
						  formData: {
						    key: updateUrl,
						    policy: aliyunOssKey.policy,
						    OSSAccessKeyId: aliyunOssKey.accessKeyId,
						    signature: aliyunOssKey.signature,
						  },
						  success: (res) => {
						    if (res.statusCode === 204) {
								fileData.url = aliRes.visitPrefix + "/" + updateUrl;
								requestInfo.onEachUpdate && requestInfo.onEachUpdate({
									url: fileData.url,
									...fileData
								});
								fileList.push(fileData.url);
								if (len - 1 > i) {
									uploadFile(i + 1);
								} else {
									resolve(fileList);
								}
						    } else {
								reject(res);
							}
						  },
						  fail: err => {
						    reject(err);
						  }
						});
					}
				});
			} else {
				reject({
					errMsg: "getAliToken",
					statusCode: 0
				});
			}
		} else {
			reject({
				errMsg: "files ",
				statusCode: 0
			});
		};
	});
}
// URL
export const urlUpload = function(requestInfo, dataFactory) {
	return new Promise((resolve, reject) => {
		// Content-Type
		if (requestInfo.header['Content-Type']) {
			delete requestInfo.header['Content-Type'];
		}
		// Content-Type
		if (requestInfo.header['content-type']) {
			delete requestInfo.header['content-type'];
		}
		if (Array.isArray(requestInfo.files)) {


























































			const len = requestInfo.files.length - 1;
			let fileList = new Array;
			fileUpload(0);

			function fileUpload(i) {
				let item = requestInfo.files[i];
				let fileData = {
					fileIndex: i,
					files: requestInfo.files,
					...item
				};
				let config = {
					url: requestInfo.url,
					filePath: item.path,
					header: requestInfo.header, //
					name: requestInfo.name || "file",
					success: (response) => {
						//
						if (requestInfo.isFactory && dataFactory) {
							//
							dataFactory({
								...requestInfo,
								response: response,
							}).then(data => {
								fileList.push(data);
								requestInfo.onEachUpdate && requestInfo.onEachUpdate({
									data: data,
									...fileData
								});
								if (len <= i) {
									resolve(fileList);
								} else {
									fileUpload(i + 1);
								}
							},err => {
								reject(err);
							});
						} else {
							requestInfo.onEachUpdate && requestInfo.onEachUpdate({
								data: response,
								...fileData
							});
							fileList.push(response);
							if (len <= i) {
								resolve(fileList);
							} else {
								fileUpload(i + 1);
							}
						}
					},
					fail: (err) => {
						reject(err);
					}
				};
				if (requestInfo.data) {
					config.formData = requestInfo.data;
				}
				const uploadTask = uni.uploadFile(config);
				uploadTask.onProgressUpdate(res => {
					requestInfo.onProgressUpdate && requestInfo.onProgressUpdate(Object.assign({}, fileData, res));
				});
			}

		} else {
			reject({
				errMsg: "files ",
				statusCode: 0
			});
		}
	});
}
